@import url("https://fonts.googleapis.com/css2?family=Raleway:wght@500&display=swap");

html {
    max-width: 100vw;
    scroll-behavior: smooth;
    overflow-x: hidden;
}

body {
    top: 0;
    left: 0;
    padding: 0;
    margin: 0;
    scroll-behavior: smooth;
    overflow-x: hidden;

    font-family: "Raleway", sans-serif;
}

nav {
    position: fixed;
    z-index: 999;
    top: 0;
    width: 100vw;
    height: 50px;
    background: white;
    box-shadow: 0 0 5px black;
}

nav.dark {
    background: #212121;
    box-shadow: 0 0 5px grey;
}

nav img {
    position: absolute;
    top: 0;
    width: 50px;
    height: 50px;
    left: 5vw;
}

nav .nav-link {
    width: 100%;
    margin-top: 15px;

    display: flex;
    justify-content: center;
}

nav .nav-link a {
    text-decoration: none;
    color: black;
    margin: 0 2vw;
}

nav .nav-link a::after {
    content: "";
    width: 0;
    height: 2px;
    background-color: black;
    margin: auto;
    display: block;

    transition: width 0.3s linear;
}

nav .nav-link a:hover::after {
    width: 100%;
}

nav.dark .nav-link a {
    color: white;
}

nav.dark .nav-link a::after {
    background-color: white;
}

nav .nav-link a:nth-last-child(1) {
    background: aqua;
    border-radius: 5px;
    color: white;
    padding: 5px 10px;
    transform: translateY(-5px);

    transition: 0.3s;
}

nav .nav-link a:nth-last-child(1)::after {
    display: none;
}

nav .nav-link a:hover {
    opacity: 0.7;
}


/* Dark Mode */
nav .btn-container {
    position: absolute;
    top: 10px;
    right: 5vw;
    display: table-cell;
    vertical-align: middle;
    text-align: center;
}

nav .btn-container i {
    display: inline-block;
    position: relative;
    top: -9px;
}

nav label {
    font-size: 13px;
    color: #424242;
    font-weight: 500;
}

nav .btn-color-mode-switch {
    display: inline-block;
    margin: 0px;
    position: relative;
}

nav .btn-color-mode-switch>label.btn-color-mode-switch-inner {
    margin: 0px;
    cursor: pointer;
    width: 140px;
    height: 30px;
    background: #e0e0e0;
    border-radius: 26px;
    overflow: hidden;
    position: relative;
    transition: all 0.3s ease;
    /*box-shadow: 0px 0px 8px 0px rgba(17, 17, 17, 0.34) inset;*/
    display: block;
}

nav .btn-color-mode-switch>label.btn-color-mode-switch-inner:before {
    content: attr(data-on);
    position: absolute;
    font-size: 12px;
    font-weight: 500;
    top: 7px;
    right: 20px;
}

nav .btn-color-mode-switch>label.btn-color-mode-switch-inner:after {
    content: attr(data-off);
    width: 70px;
    height: 16px;
    background: #fff;
    border-radius: 26px;
    position: absolute;
    left: 2px;
    top: 2px;
    text-align: center;
    transition: all 0.3s ease;
    box-shadow: 0px 0px 6px -2px #111;
    padding: 5px 0px;
}

nav .btn-color-mode-switch>.alert {
    display: none;
    background: #ff9800;
    border: none;
    color: #fff;
}

nav .btn-color-mode-switch input[type="checkbox"] {
    cursor: pointer;
    width: 50px;
    height: 25px;
    opacity: 0;
    position: absolute;
    top: 0;
    z-index: 1;
    margin: 0px;
}

nav .btn-color-mode-switch input[type="checkbox"]:checked+label.btn-color-mode-switch-inner {
    background: #151515;
    color: #fff;
}

nav .btn-color-mode-switch input[type="checkbox"]:checked+label.btn-color-mode-switch-inner:after {
    content: attr(data-on);
    left: 68px;
    background: #3c3c3c;
}

nav .btn-color-mode-switch input[type="checkbox"]:checked+label.btn-color-mode-switch-inner:before {
    content: attr(data-off);
    right: auto;
    left: 20px;
}

nav .btn-color-mode-switch input[type="checkbox"]:checked~.alert {
    display: block;
}

nav .dark-preview {
    background: #0d0d0d;
}

nav .white-preview {
    background: #fff;
}

/* NavMenu Toggle */
nav .icon-menu {
    display: none;
    position: absolute;
    top: 15px;
    right: 5vw;

    --gap: 5px;
    --height-bar: 2.5px;
    --pos-y-bar-one: 0;
    --pos-y-bar-three: 0;
    --scale-bar: 1;
    --rotate-bar-one: 0;
    --rotate-bar-three: 0;
    width: 25px;
    flex-direction: column;
    gap: var(--gap);
    cursor: pointer;
}

nav .bar {
    position: relative;
    height: var(--height-bar);
    width: 100%;
    border-radius: 0.5rem;
    background-color: aqua;
}

nav .bar--1 {
    top: var(--pos-y-bar-one);
    transform: rotate(var(--rotate-bar-one));
    transition: top 200ms 100ms, transform 100ms;
}

nav .bar--2 {
    transform: scaleX(var(--scale-bar));
    transition: transform 150ms 100ms;
}

nav .bar--3 {
    bottom: var(--pos-y-bar-three);
    transform: rotate(var(--rotate-bar-three));
    transition: bottom 200ms 100ms, transform 100ms;
}

nav .check-icon:checked+.icon-menu>.bar--1 {
    transition: top 200ms, transform 200ms 100ms;
}

nav .check-icon:checked+.icon-menu>.bar--3 {
    transition: bottom 200ms, transform 200ms 100ms;
}

nav .check-icon:checked+.icon-menu {
    --pos-y-bar-one: calc(var(--gap) + var(--height-bar));
    --pos-y-bar-three: calc(var(--gap) + var(--height-bar));
    --scale-bar: 0;
    --rotate-bar-one: 45deg;
    --rotate-bar-three: -45deg;
}

@media (max-width: 900px) {
    nav .icon-menu {
        display: flex;
    }
    nav .btn-container {
        right: 15vw;
    }

    nav .nav-link {
        position: absolute;
        width: 90%;
        display: flex;
        padding: 50px 5% 0 5%;
        flex-direction: column;
        text-align: center;
        background: white;
        z-index: -1;
        top: -50vh;

        transition: 0.8s ease-out;
    }
    nav.dark .nav-link {
        background: #212121;
    }
    nav .nav-link a {
        padding: 2vh 0;
    }
    nav .nav-link a::after {
        display: none;
    }
    nav .nav-link a:hover {
        background-color: grey;
    }

    nav .nav-link.active {
        top: 0;
    }
}

/* Footer */
footer {
    width: 80vw;
    padding: 5% 10%;
    background: darkcyan;

    display: flex;
    flex-wrap: wrap;
}

footer .item {
    margin: 2vw 5vw;
    display: flex;
    flex-direction: column;
}

footer .item:nth-last-child(2) {
    width: 20%;
    display: block;
}

footer .item:nth-last-child(2) p {
    color: white;
}

footer .item:nth-last-child(2) svg {
    height: 25px;
    fill: #cccccc;
    margin-right: 5%;
}

footer .item:nth-last-child(2) svg:hover {
    fill: white;
}

footer .item h3 {
    color: white;
}

footer .item a {
    margin: 5% 0;
    text-decoration: none;
    color: #cccccc;
}

footer .item a:hover {
    color: white;
}

footer .brand {
    width: 20%;
    margin: 2vw 2vw;
    color: white;
}

footer .brand img {
    width: 100px;
    height: 100px;
}

footer .copyright {
    width: 100%;
    text-align: center;
    color: white;
}

@media (max-width: 900px) {
    footer .brand {
        width: 100%;
    }
}

/* Color Style */
.c-white {
    color: white;
}

.c-black {
    color: black;
}

.c-grey {
    color: grey;
}

.c-aqua {
    color: aqua;
}