header {
    width: 100vw;
    height: 600px;
    max-width: 100vw;
    margin-top: 50px;

    background-repeat: no-repeat;
    background-size: cover;
    overflow: hidden;
}

header h1 {
    position: absolute;
    top: 250px;
    left: 5vw;
    font-size: min(7vw, 40px);
    color: white;
}

header a {
    position: absolute;
    left: 5vw;
    top: 400px;
    padding: 10px;
    text-decoration: none;
    color: white;
    background: darkcyan;
    border-radius: 5px;

    transition: 0.3s;
}
header a:hover {
    background: aqua;
}

/* Corousel */
header .slider {
    position: relative;
    width: 200vw;
    height: 600px;
    left: 0;
    z-index: -1;
    display: flex;

    animation: 20s corousel infinite;
}
header .slider .container {
    width: 100vw;
    height: 100%;
    overflow: hidden;
}
header .slider .container img {
    width: 100%;
    min-width: 900px;
    height: 100%;
    float: left;
    background-size: cover;
    background-repeat: no-repeat;
}

@keyframes corousel {
    0% { left: 0; }
    25% { left: 0; }
    50% { left: -100%; }
    75% { left: -100%; }
}

/* Section */
section {
    width: 100%;
}
section.dark {
    background: #212121;
}

section.dark .c-white { color: black; }
section.dark .c-black { color: white; }

section h2 { font-size: max(3vw, 25px);}
section h3 { font-size: max(1.5vw, 20px);}
section p { font-size: max(1vw, 15px);}

/* Container 1 */
section .container-1 {
    width: 90%;
    padding: 5%;
    margin-bottom: 10%;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}

section .container-1 h2 {
    width: 100%;
    text-align: center;
}

section .container-1 .item {
    width: 25%;
    margin: 5% 2.5%;

    display: flex;
    flex-direction: column;
    text-align: center;
    justify-content: center;
}

section .container-1 .item svg {
    height: 100px;
    fill: aqua;
}

@media (max-width: 900px) {
    section .container-1 .item {
        width: 100%;
    }
}

/* Container 2 */
section .container-2 {
    width: 90%;
    height: 300px;
    padding: 5%;

    background-color: darkcyan;
    
    display: flex;
}

section .container-2 img {
    border-radius: 10px;
}

section .container-2 .content {
    width: 50%;
    padding: 5%;
}

section .container-2 .content h2 {
    color: white;
}
section .container-2 .content p {
    color: white;
}

@media (max-width: 900px) {
    section .container-2 {
        flex-wrap: wrap;
        height: 100%;
    }
    section .container-2 img {
        width: 90%;
    }
    section .container-2 .content {
        width: 80%;
    }
}

/* Container 3 */
section .container-3 {
    width: 100%;

    display: flex;
    justify-content: center;
    align-items: center;
}

section .container-3 .item {
    margin: 2vw;
    width: 10%;
    background: inherit;
    border-radius: 10px;
    box-shadow: 0 0 5px grey;
    text-align: center;
}

@media (max-width: 900px) {
    section .container-3 .item {
        width: 30%;
    }
}

/* Container 4 */
section .container-4 {
    width: 90%;
    padding: 5%;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}

section .container-4 h2 {
    width: 100%;
    text-align: center;
}

section .container-4 .item {
    width: 25%;
    margin: 2.5%;

    display: flex;
    flex-direction: column;
    text-align: center;
    justify-content: center;
}

section .container-4 .item img {
    width: 100%;
    background-size: cover;
    border-radius: 10px;
}

@media (max-width: 900px) {
    section .container-4 .item {
        width: 100%;
    }
}

/* Container 5 */
section .container-5 {
    width: 100%;
    padding: 5%;

    display: flex;
}

section .container-5 h2 {
    width: 50%;
    text-align: center;
    align-self: center;
}

section .container-5 form {
    width: 50%;
    padding: 5%;
}

section .container-5 form input {
    width: 50%;
    background: none;
    border: none;
    border-bottom: 1px solid black ;
    outline: none;
    color: grey;
}

section .container-5 form textarea {
    width: 50%;
    height: 100px;
    background: none;
    border: none;
    border-bottom: 1px solid black ;
    resize: vertical;
    outline: none;
    color: grey;
}

section.dark .container-5 form input, section.dark .container-5 form textarea {
    border-bottom: 1px solid white ;
}

section .container-5 form button {
    padding: 10px 2vw;
    
    cursor: pointer;
    border: none;
    background: aqua;
    border-radius: 5px;
    color: white;
    
    transition: 0.3s;
}
section .container-5 form button:hover {
    opacity: 0.7;
}

@media (max-width: 900px) {
    section .container-5 {
        flex-wrap: wrap;
    }

    section .container-5 form {
        width: 100%;
    }
}

/* Container 6 */
section .container-6 {
    width: 90%;
    height: 300px;
    padding: 5%;

    background-color: darkcyan;
    
    display: flex;
    flex-direction: column;
    justify-content: center;
    text-align: center;
}

section .container-6 h2 {
    color: white;
}
section .container-6 p {
    color: white;
}